import React from "react"
import { useLocation } from "react-router-dom"

const Back = ({ title }) => {
  const location = useLocation()

  return (
    <>
      <section className={location.pathname.split("/")[1] === "team" ? "back teambg" : location.pathname.split("/")[1] === "pricing" ? "back pricebg" : location.pathname.split("/")[1] === "contact" ? "back contactbg" : "back"}>
        <h2>Home / {location.pathname.split("/")[1]}</h2>
        <h1>{title}</h1>
      </section>
      <div className='margin'></div>
    </>
  )
}

export default Back
